*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body{
  background: #EEE;
}
.heading{
  text-align: center;
  color: rgba(0, 0, 139, 0.651);
  font-size: 2em;
  padding-top: 50px;
}
.score{
  margin: 50px auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 500px;
  height: 300px;
  background: rgba(0, 0, 0, 0.1);
  box-shadow: 0 6px 6px rgba(0, 0, 0, 0.459);
  border-radius: 20px;
}
span{
  transition: all 0.3s ease;
  color: crimson;
  font-weight: bold;
}